@import '../../variables.scss';

.footer {
    padding: 25px 0 50px;
    border-top: 1px solid #7F7F7F;
    position: relative;

    &::after {
        display: block;
        width: 100%;
        height: 0;
        content: '';
        border-top: 1px solid #7F7F7F;
        position: absolute;
        top: 75px;
        left: 0;
    }

    .container {
        display: flex;
        justify-content: space-between;
        column-gap: 10px;

        @media (max-width: 890px) {
            gap: 40px;
            flex-direction: column;
        }
    }

    &_list {
        display: flex;
        gap: 40px;

        @media (max-width: 1045px) {
            gap: 15px;
        }

        @media (max-width: 890px) {
            justify-content: center;
        }

        @media (max-width: 440px) {
            row-gap: 30px;
            flex-wrap: wrap;
        }
    }

    &_item {
        display: flex;
        flex-direction: column;
        gap: 22px;
        width: 165px;

        @media (max-width: 767px) {
            gap: 16px;
        }
    }

    &_wrap {
        width: 230px;

        @media (max-width: 890px) {
            width: 100%;
        }
    }

    &_logo_wrap {
        margin-bottom: 40px;
    }

    &_link {
        @extend %sidebar_text;
        color: #292A33;

        &-inst {
            display: flex;
            align-items: center;
            gap: 12px;
            margin-bottom: 20px;
        }
    }

    &_text {
        @extend %sidebar_text;
        line-height: 160%;
        color: #292A33;
        text-transform: capitalize;
    }

    &_title {
        margin-bottom: 40px;
        @extend %h3;
        color: #292A33;

        @media (max-width: 767px) {
            margin-bottom: 0;
        }
    }
}