.otherNav {
    &_wrap {
        display: flex;
        align-items: center;
        gap: 24px;

        @media (max-width: 999px) {
            gap: 8px;
        }
    }

    &_link {
        @media (max-width: 424px) {
            display: none;
        }
    }
}

.cart {
    &_wrap {
        display: flex;
        align-items: center;
        gap: 10px;
    }

    &_quantity {
        font-family: 'Lato', sans-serif;
        font-size: 16px;
        font-weight: 700;
        color: #fff;
        width: 20px;
        height: 20px;
        background: #DE1616;
        border-radius: 50%;
        text-align: center;
    }

    &_price {
        font-weight: 500;
        font-size: 14px;   
    }
}