@import '../../variables.scss';

.modal {
    background: #fff;
    max-width: 1040px;
    display: grid;
    grid-template-columns: 430px 1fr;
    position: relative;

    @media (max-width: 900px) {
        grid-template-columns: 1fr 1fr;
    }

    // @media (max-width: 767px) {
    //     grid-template-columns: 1fr;
    // }

    img {
        @media (max-width: 767px) {
            // width: 260px;
        }
    }
  
    &_overlay {
        width: 100vw;
        height: 100vh;
        padding: 0 15px;
        position: fixed;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.5);
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &_close_btn {
        position: absolute;
        top: 20px;
        right: 20px;

        font-family: 'Josefin Sans';
        font-weight: 400;
        font-size: 22px;
        line-height: 22px;
        letter-spacing: 0.05em;
        opacity: 0.3;
    }

    &_wrap {
        padding: 35px 40px;

        @media (max-width: 767px) {
            padding: 17px 20px;
        }
    }

    &_title {
        @extend %h2;
        padding-bottom: 15px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        margin-bottom: 15px;
    }

    &_price {
        @extend %price;
        margin-bottom: 23px;
    }   

    &_description {
        font-weight: 300;
        font-size: 18px;
        line-height: 160%;
        letter-spacing: 0.05em;
        padding-bottom: 15px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        margin-bottom: 15px;

        @media (max-width: 767px) {
            font-size: 14px;
        }
    }

    &_stock { 
        @extend %price;
        color: #DE1616;
        padding-bottom: 15px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        margin-bottom: 15px;
    }

    &_qty {
        display: flex;
        align-items: center;
        gap: 10px;
        padding-bottom: 15px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        margin-bottom: 15px;

        &_label {
            font-weight: 400;
            font-size: 18px;
            line-height: 18px;
            letter-spacing: 0.05em;

            @media (max-width: 767px) {
                font-size: 14px;
                line-height: 14px;
            }
        }

        &_inp {
            width: 30px;
            height: 30px;
            border: 1px solid rgba(0, 0, 0, 0.3);

            font-family: 'Josefin Sans';
            font-weight: 300;
            font-size: 18px;
            line-height: 18px;
            text-align: center;

            &:focus {
                outline: none;
                border: 1px solid rgba(0, 0, 0, 0.8);
            }
        }
    }

    &_info {
        display: flex;
        flex-direction: column;
        gap: 20px;

        &_item {
            font-weight: 400;
            font-size: 18px;
            line-height: 18px;
            letter-spacing: 0.05em;

            @media (max-width: 767px) {
                font-size: 16px;
                line-height: 16px;
            }

            span {
                margin-left: 5px;
                font-weight: 300;
            }
        }
    }
}