@import '../../../../../variables.scss';

.dealOfWeek {
    margin-bottom: 90px;

    &_slider {
        margin: 60px 0 40px;
    }

    &_item {
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        justify-content: center;
        text-align: center;

        img {
            justify-self: end;
        }
        
        &_wrap {
            max-width: 300px;
            padding: 25px 22px;
            border-width: 1px 1px 1px 0px;
            border-style: solid;
            border-color: #7F7F7F;

            @media (max-width: 767px) {
                padding: 12.5px 11px;
            }
        }

        &_title {
            @extend %h4;
            margin-bottom: 10px;
        }

        &_rating_wrap {
            margin-bottom: 15px;
        }

        &_price {
            @extend %price;
            margin-bottom: 14px;
        }

        &_line {
            display: block;
            width: 100%;
            height: 1px;
            background-color: rgba(0, 0, 0, 0.1);
            margin-bottom: 10px;
        }

        &_description {
            font-family: 'Josefin Sans';
            font-weight: 300;
            font-size: 14px;
            line-height: 160%;
            letter-spacing: 0.05em;
            margin-bottom: 20px;
            text-align: left;

            @media (max-width: 767px) {
                font-size: 12px;
                line-height: 140%;
            }
        }

        &_btn_wrap {
            display: flex;
            align-items: center;
            gap: 25px;
        }

        &_btn {
            margin-right: auto;
        }
    }
}