@import '../../../variables.scss';

.register {
    padding: 100px 0;

    .container {
        width: 300px;
        margin: 0 auto;
    }

    &_title {
        @extend %h1;
        text-align: center;
        margin-bottom: 60px;
    }

    &_form {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;        
    }
}