@import '../../variables';

.likedProducts {
  &_title {
    margin-bottom: 30px;
    text-align: left;

    &::after {
      margin: 10px 0 0;
    }
  }

  &_list {
    display: grid;
    grid-template-columns: repeat(auto-fit, 260px);
    justify-content: center;
    gap: 40px;
    margin-bottom: 20px;

    @media (max-width: 909px) {
      // justify-content: center;
      // grid-template-columns: repeat(auto-fit, 190px);
    }
  }

  &_btn {
    @extend %btn_text;
    margin: 0 auto;
    font-weight: 400;
    padding: 10px 8px;
    border-radius: 10px;
    transition: all 0.4s;

    &:hover {
      background: #d7d7d7;
    }

    &:active {
      background: #a0a0a0;
    }
  }
}
