@import '../../../../../variables.scss';

.shopByCat {
    margin-bottom: 90px;

    &_slider {
        margin-top: 60px;
        margin-bottom: 40px;

        &_item {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
        

    &_item_title {
        display: flex;
        justify-content: center;
        padding: 20px 0;

        @extend %h3;   
    }   
}