@import '../../../../../variables.scss'; 

.baner {
    margin-bottom: 90px;

    .container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        align-items: center;
        gap: 40px;

        @media (max-width: 767px) {
            grid-template-columns: 1fr;
        }
    }

    &_item {
        max-width: 560px;
        height: 560px;
        padding: 40px;
        background-size: cover;
        background-repeat: no-repeat;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        @media (max-width: 767px) {
            margin: 0 auto;
            width: 90%;
        }
    }

    &_firstItem {
        background-image: url("../../../../../images/baner/baner-1.png");
        background-position: top right;

        &_title {
            @extend %h2;
            margin-bottom: 10px;
        }

        &_subtitle {
            width: 205px;
            @extend %body_text;
        }
    }

    &_secondItem {
        background-image: url("../../../../../images/baner/baner-2.png");
        background-position: top left;
        align-items: flex-end;

        &_title {
            @extend %h2;
            width: 235px;
            line-height: 160%;
            text-align: right;
            color: #FFFFFF;
        }
    }
}