@import '../../../../../../variables.scss';

.shop_gridItem {
  box-shadow: 0px 6px 34px rgba(0, 0, 0, 0.15);
  padding-bottom: 10px;
  list-style: none;

    &_img_wrap {
      position: relative;
      margin-bottom: 10px;
    }

    &_overlay {
      position: absolute;
      top: 15px;
      right: 15px;
      height: 230px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      &_small {
        @media (max-width: 909px) {
          height: 160px;
        }
      }
    }

    &_btnWrap {
      display: flex;
      flex-direction: column;
      gap: 15px;
    }

    &_title {
      margin-bottom: 10px;
      @extend %h4;
      text-align: center;
    }

    &_rating_wrap {
      width: 120px;
      margin: 0 auto 10px;
    }

    &_price {
      @extend %price;
      text-align: center;
    }
  }