$black: #292A33;
$grey: #7F7F7F;

%h1 {
    font-family: 'Cormorant Garamond';
    font-weight: 300;
    font-size: 42px;
    line-height: 51px;
    letter-spacing: 0.1em;
    color: #000000;

    @media (max-width: 767px) {
        font-size: 36px;
        line-height: 44px;
    }
}

%h2 {
    font-family: 'Cormorant Garamond';
    font-weight: 400;
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 0.1em;
    text-transform: capitalize;
    color: #000000;

    @media (max-width: 767px) {
        font-size: 24px;
        line-height: 28px;
    }
}

%h3 {
    font-family: 'Cormorant Garamond';
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.1em;
    text-transform: capitalize;
    color: #000000;

    @media (max-width: 767px) {
        font-size: 20px;
        line-height: 24px;
    }
}

%h4 {
    font-family: 'Cormorant Garamond';
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.1em;
    color: #000000;

    @media (max-width: 767px) {
        font-size: 16px;
        line-height: 18px;
    }
}

%btn_text {
    font-family: 'Josefin Sans';
    font-weight: 300;
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    color: #000000;

    @media (max-width: 767px) {
        font-size: 16px;
        line-height: 16px;
    }
}

%price {
    font-family: 'Josefin Sans';
    font-weight: 300;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 0.05em;
    color: #000000;

    @media (max-width: 767px) {
        font-size: 18px;
        line-height: 18px;
    }
}

%menu_text {
    font-family: 'Josefin Sans';
    font-weight: 300;
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 0.05em;
    text-transform: capitalize;
    color: #000000;

    @media (max-width: 767px) {
        font-size: 14px;
        line-height: 14px;
    }
}

%body_text {
    font-family: 'Josefin Sans';
    font-weight: 300;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: 0.05em;
    color: #000000;

    @media (max-width: 767px) {
        font-size: 14px;
        line-height: 140%;
    }
}

%sidebar_text {
    font-family: 'Josefin Sans';
    font-weight: 300;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: capitalize;
    color: #000000;

    @media (max-width: 767px) {
        font-size: 12px;
        line-height: 12px;
    }
}

%text_underline {
    font-family: 'Josefin Sans';
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    text-decoration-line: underline;
    text-transform: capitalize;

    @media (max-width: 767px) {
        font-size: 12px;
        line-height: 12px;
    }
}

%small_btn_text {
    font-family: 'Josefin Sans';
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 0.05em;
    text-transform: capitalize;
    color: #000000;

    @media (max-width: 767px) {
        font-size: 10px;
        line-height: 10px;
    }
}