.productTag {
    width: 260px;

    &_title {
        text-align: left;
        margin-bottom: 25px;

        &::after {
            margin: 10px 0 0;
        }
    }

    &_list {
        display: flex;
        flex-wrap: wrap;
        gap: 7px;
    }

    &_btn {
        padding: 7px 14px;
        border: 2px solid transparent;
        background: #D9D9D9;

        font-family: 'Josefin Sans';
        font-weight: 400;
        font-size: 16px;
        line-height: 16px;
        letter-spacing: 0.05em;
        text-transform: capitalize;
        color: rgba(0, 0, 0, 0.5);

        &-active {
            color: rgba(0, 0, 0, 0.8);;
            border: 2px solid #333;
        }
    }
}